"use client"

import { useState, useEffect, useRef } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { 
  ChevronLeft, Send, Sparkles, User, Loader2, Bot
} from "lucide-react"

// ★ 중요: stompjs 전체를 가져오지 않고 특정 방식이나 외부 라이브러리 형태로 처리합니다.
import SockJS from "sockjs-client"
import Stomp from "stompjs"

export default function AIChatPage() {
  const router = useRouter()
  const [userName, setUserName] = useState("")
  const [messages, setMessages] = useState<any[]>([])
  const [inputValue, setInputValue] = useState("")
  const [isTyping, setIsTyping] = useState(false)
  const [isConnected, setIsConnected] = useState(false)
  const [currentRoomId, setCurrentRoomId] = useState<string | null>(null)
  
  const stompClient = useRef<any>(null)
  const scrollRef = useRef<HTMLDivElement>(null)

  const API_BASE = "http://sparta-nextme.xyz:18080"

  // 메시지 수신 핸들러
  const onMessageReceived = (payload: any) => {
    try {
      const newMessage = JSON.parse(payload.body)
      setMessages((prev) => [...prev, newMessage])
      setIsTyping(false)
      scrollToBottom()
    } catch (e) {
      console.error("Parsing error", e)
    }
  }

  // 채팅방 초기화 및 설정
  const setupChat = async (token: string, userId: string | null) => {
    try {
      const res = await fetch(`${API_BASE}/v1/chats/room?roomType=AI`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      const aiRooms = data.rooms || data
      let roomId = aiRooms?.[0]?.chatRoomId || aiRooms?.[0]?.id

      if (!roomId) {
        const createRes = await fetch(`${API_BASE}/v1/chats/room`, {
          method: "POST",
          headers: { "Content-Type": "application/json", "Authorization": `Bearer ${token}` },
          body: JSON.stringify({ roomType: "AI", userId })
        })
        const newData = await createRes.json()
        roomId = newData.chatRoomId
      }

      if (roomId) {
        setCurrentRoomId(roomId)
        const msgRes = await fetch(`${API_BASE}/v1/chats/message/${roomId}?size=20`, {
          headers: { "Authorization": `Bearer ${token}` }
        })
        const msgData = await msgRes.json()
        const fetchedMsgs = Array.isArray(msgData) ? msgData : (msgData.messages || [])
        setMessages(fetchedMsgs.reverse())
        
        // ★ STOMP 구독
        if (stompClient.current) {
          stompClient.current.subscribe(`/topic/chat.room.${roomId}`, onMessageReceived)
        }
        
        await fetch(`${API_BASE}/v1/chats/room/${roomId}/join`, {
          method: "POST",
          headers: { "Authorization": `Bearer ${token}` }
        })
        scrollToBottom()
      }
    } catch (e) {
      console.error("Chat setup failed", e)
    }
  }

  useEffect(() => {
    const name = localStorage.getItem("name") || "사용자"
    const token = localStorage.getItem("accessToken")
    const userId = localStorage.getItem("userId")
    setUserName(name)

    if (!token) {
      router.push("/login")
      return
    }

    // ★ [해결책] stompjs의 net 모듈 문제를 피하기 위한 SockJS & Stomp 연결 방식
    const socket = new SockJS(`${API_BASE}/ws/chat`)
    
    // Stomp.over(socket) 대신 직접 stompjs 내부의 웹브라우저용 클라이언트를 사용하도록 시도
    const client = Stomp.over(socket)
    client.debug = (str) => {} // 디버그 끄기

    client.connect(
      { Authorization: `Bearer ${token}` },
      () => {
        setIsConnected(true)
        stompClient.current = client
        setupChat(token, userId)
      },
      (error) => {
        console.error("Connection error:", error)
        setIsConnected(false)
      }
    )

    return () => {
      if (stompClient.current && stompClient.current.connected) {
        stompClient.current.disconnect(() => {})
      }
    }
  }, [])

  const sendMessage = (e: React.FormEvent) => {
    e.preventDefault()
    if (!inputValue.trim() || !stompClient.current || !currentRoomId) return

    const payload = { content: inputValue, roomType: "AI" }
    stompClient.current.send(`/app/chat.send/${currentRoomId}`, {}, JSON.stringify(payload))
    
    setInputValue("")
    setIsTyping(true)
    scrollToBottom()
  }

  const scrollToBottom = () => {
    setTimeout(() => {
      if (scrollRef.current) scrollRef.current.scrollTop = scrollRef.current.scrollHeight
    }, 100)
  }

  return (
    <div className="min-h-screen bg-background text-foreground flex flex-col">
      <header className="border-b bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <div className="flex items-center gap-2">
            <Button variant="ghost" size="icon" onClick={() => router.back()}><ChevronLeft className="h-5 w-5" /></Button>
            <Link href="/"><Image src="/logo.png" alt="Logo" width={110} height={30} className="h-7 w-auto" /></Link>
          </div>
          <div className="flex flex-col items-end">
             <span className="text-[10px] font-black text-primary uppercase">AI Advisor</span>
             <span className={`text-[9px] ${isConnected ? 'text-green-500 font-bold' : 'text-red-500'}`}>
               {isConnected ? '● Connected' : '○ Offline'}
             </span>
          </div>
        </div>
      </header>

      <main className="container mx-auto max-w-3xl flex-1 flex flex-col p-4 overflow-hidden">
        <div ref={scrollRef} className="flex-1 overflow-y-auto space-y-6 mb-4 custom-scrollbar">
          {messages.map((msg, idx) => {
            const isMine = msg.senderId !== "AI"
            return (
              <div key={idx} className={`flex ${isMine ? 'justify-end' : 'justify-start'} animate-in fade-in slide-in-from-bottom-2`}>
                <div className={`flex gap-3 max-w-[85%] ${isMine ? 'flex-row-reverse' : ''}`}>
                  <div className={`h-10 w-10 rounded-xl flex items-center justify-center shrink-0 border ${isMine ? 'bg-primary' : 'bg-card'}`}>
                    {isMine ? <User className="h-5 w-5 text-white" /> : <Sparkles className="h-5 w-5 text-primary" />}
                  </div>
                  <div className={`flex flex-col gap-1 ${isMine ? 'items-end' : 'items-start'}`}>
                    <Card className={`p-4 rounded-2xl ${isMine ? 'bg-primary text-white border-none shadow-md' : 'bg-card border-border/50 shadow-sm'}`}>
                      <p className="text-sm font-medium whitespace-pre-wrap">{msg.content}</p>
                    </Card>
                    <span className="text-[9px] text-muted-foreground opacity-70">
                      {new Date(msg.createdAt).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                    </span>
                  </div>
                </div>
              </div>
            )
          })}
          {isTyping && <div className="text-xs text-primary animate-pulse font-bold ml-14">AI 비서가 생각 중...</div>}
        </div>

        <form onSubmit={sendMessage} className="relative pb-6">
          <Input 
            value={inputValue}
            onChange={(e) => setInputValue(e.target.value)}
            placeholder={isConnected ? "AI에게 질문하세요..." : "서버 연결 대기 중..."}
            className="h-14 pl-6 pr-14 rounded-2xl bg-card border-border/50 shadow-lg text-base"
            disabled={!isConnected}
          />
          <Button 
            type="submit" 
            className="absolute right-2 top-2 h-10 w-10 rounded-xl shadow-md transition-transform active:scale-95" 
            disabled={!isConnected || !inputValue.trim()}
          >
            <Send className="h-5 w-5" />
          </Button>
        </form>
      </main>
    </div>
  )
}